import React, { useState } from "react";

const DataTable = ({ columns, data, onAdd, onEdit, onDelete, isEditor, addButtonText = "Add Row" }) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [editingId, setEditingId] = useState(null);
  const [editData, setEditData] = useState({});
  const [showAddModal, setShowAddModal] = useState(false);
  const [newRowData, setNewRowData] = useState({});

  const filteredData = data.filter((row) => {
    const searchLower = searchTerm.toLowerCase();
    return columns.some((col) => {
      const value = row[col.key] || "";
      return String(value).toLowerCase().includes(searchLower);
    });
  });

  const handleEdit = (row) => {
    setEditingId(row.id);
    setEditData({ ...row });
  };

  const handleSave = () => {
    onEdit(editingId, editData);
    setEditingId(null);
    setEditData({});
  };

  const handleCancel = () => {
    setEditingId(null);
    setEditData({});
  };

  const handleAdd = () => {
    onAdd(newRowData);
    setNewRowData({});
    setShowAddModal(false);
  };

  return (
    <div>
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "1.5rem", gap: "1rem", flexWrap: "wrap" }}>
        <div className="search-box" style={{ flex: "1", maxWidth: "400px", marginBottom: "0" }}>
          <span className="search-icon"></span>
          <input
            type="text"
            className="search-input"
            placeholder="Search..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            data-testid="table-search"
          />
        </div>
        {isEditor && (
          <button
            className="btn btn-primary btn-sm"
            onClick={() => setShowAddModal(true)}
            data-testid="add-row-btn"
          >
            + {addButtonText}
          </button>
        )}
      </div>

      <div className="table-container">
        <table className="data-table">
          <thead>
            <tr>
              {columns.map((col) => (
                <th key={col.key}>{col.label}</th>
              ))}
              {isEditor && <th>Actions</th>}
            </tr>
          </thead>
          <tbody>
            {filteredData.length === 0 ? (
              <tr>
                <td colSpan={columns.length + (isEditor ? 1 : 0)} style={{ textAlign: "center", padding: "2rem", color: "#718096" }}>
                  No data available. {isEditor && "Click 'Add Row' to get started."}
                </td>
              </tr>
            ) : (
              filteredData.map((row) => (
                <tr key={row.id}>
                  {columns.map((col) => (
                    <td key={col.key}>
                      {editingId === row.id ? (
                        <input
                          type="text"
                          className="input"
                          style={{ padding: "0.5rem", fontSize: "0.875rem" }}
                          value={editData[col.key] || ""}
                          onChange={(e) => setEditData({ ...editData, [col.key]: e.target.value })}
                        />
                      ) : (
                        row[col.key] || "-"
                      )}
                    </td>
                  ))}
                  {isEditor && (
                    <td>
                      {editingId === row.id ? (
                        <div style={{ display: "flex", gap: "0.5rem" }}>
                          <button className="btn btn-success btn-sm" onClick={handleSave} data-testid={`save-${row.id}`}>
                            Save
                          </button>
                          <button className="btn btn-outline btn-sm" onClick={handleCancel}>
                            Cancel
                          </button>
                        </div>
                      ) : (
                        <div style={{ display: "flex", gap: "0.5rem" }}>
                          <button className="btn btn-primary btn-sm" onClick={() => handleEdit(row)} data-testid={`edit-${row.id}`}>
                            Edit
                          </button>
                          <button className="btn btn-danger btn-sm" onClick={() => onDelete(row.id)} data-testid={`delete-${row.id}`}>
                            Delete
                          </button>
                        </div>
                      )}
                    </td>
                  )}
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {showAddModal && (
        <div className="modal-overlay" onClick={() => setShowAddModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2 className="modal-title">{addButtonText}</h2>
              <button className="close-btn" onClick={() => setShowAddModal(false)}>
                
              </button>
            </div>

            <form onSubmit={(e) => { e.preventDefault(); handleAdd(); }}>
              {columns.map((col) => (
                <div className="form-group" key={col.key}>
                  <label className="label">{col.label}</label>
                  <input
                    type="text"
                    className="input"
                    value={newRowData[col.key] || ""}
                    onChange={(e) => setNewRowData({ ...newRowData, [col.key]: e.target.value })}
                    placeholder={`Enter ${col.label.toLowerCase()}`}
                    data-testid={`new-${col.key}`}
                  />
                </div>
              ))}

              <div style={{ display: "flex", gap: "1rem", marginTop: "1.5rem" }}>
                <button type="submit" className="btn btn-primary" style={{ flex: 1 }} data-testid="submit-new-row">
                  Add
                </button>
                <button
                  type="button"
                  className="btn btn-outline"
                  onClick={() => { setShowAddModal(false); setNewRowData({}); }}
                  style={{ flex: 1 }}
                >
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default DataTable;
