import React, { useEffect, useMemo, useState } from "react";
import axios from "axios";
import { API } from "../../App";
import { useGenericTables } from "../../hooks/useGenericTables";
import { SECTION_CONFIG } from "../../sectionConfig";

const fileToDataUrl = (file) =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => resolve(reader.result);
    reader.onerror = reject;
    reader.readAsDataURL(file);
  });

const GenericSection = ({
  projectId,
  isEditor,
  sectionId,
  sectionName,
  showHeading = true
}) => {
  const config = SECTION_CONFIG[sectionId] || { tables: [], singleEntries: [] };
  const tables = config.tables || [];
  const singleEntryDefinitions = config.singleEntries || [];

  const {
    data: tableData,
    loading: tablesLoading,
    createRow,
    updateRow,
    deleteRow,
    refresh
  } = useGenericTables(projectId, sectionId, tables);

  const [activeTableKey, setActiveTableKey] = useState(tables[0]?.key || "");
  const [editingId, setEditingId] = useState(null);
  const [editData, setEditData] = useState({});
  const [newRowData, setNewRowData] = useState({});
  const [singleEntryValues, setSingleEntryValues] = useState({});
  const [singleEntriesLoading, setSingleEntriesLoading] = useState(false);

  useEffect(() => {
    setActiveTableKey(tables[0]?.key || "");
    setEditingId(null);
    setEditData({});
    setNewRowData({});
  }, [sectionId, tables]);

  useEffect(() => {
    const fetchSingleEntries = async () => {
      if (!singleEntryDefinitions.length) {
        setSingleEntryValues({});
        return;
      }
      setSingleEntriesLoading(true);
      try {
        const responses = await Promise.all(
          singleEntryDefinitions.map((entry) =>
            axios.get(`${API}/projects/${projectId}/single-entry/${entry.field}`)
          )
        );
        const nextValues = {};
        singleEntryDefinitions.forEach((entry, index) => {
          const payload = responses[index].data;
          nextValues[entry.field] = {
            content: payload?.content || "",
            image_data: payload?.image_data || null
          };
        });
        setSingleEntryValues(nextValues);
      } catch (err) {
        console.error(`Failed to load single entry fields for ${sectionId}`, err);
        setSingleEntryValues({});
      } finally {
        setSingleEntriesLoading(false);
      }
    };

    fetchSingleEntries();
  }, [projectId, sectionId, singleEntryDefinitions]);

  const activeTableDefinition = useMemo(
    () => tables.find((table) => table.key === activeTableKey),
    [activeTableKey, tables]
  );

  const activeRows = tableData[activeTableKey] || [];

  const handleAddRow = async () => {
    if (!activeTableKey) return;
    try {
      await createRow(activeTableKey, newRowData);
      setNewRowData({});
    } catch (err) {
      alert("Failed to add row");
    }
  };

  const handleEditRow = async () => {
    if (!activeTableKey || !editingId) return;
    const { id: _, ...payload } = editData;
    try {
      await updateRow(activeTableKey, editingId, payload);
      setEditingId(null);
      setEditData({});
    } catch (err) {
      alert("Failed to update row");
    }
  };

  const handleDeleteRow = async (rowId) => {
    if (!window.confirm("Delete this row?")) return;
    try {
      await deleteRow(activeTableKey, rowId);
    } catch (err) {
      alert("Failed to delete row");
    }
  };

  const handlePrefillRows = async (definition) => {
    if (!definition.prefillRows || !definition.prefillRows.length) return;
    const apiName = definition.apiName || definition.key;
    try {
      for (const row of definition.prefillRows) {
        await axios.post(
          `${API}/projects/${projectId}/sections/${sectionId}/tables/${apiName}`,
          { data: row }
        );
      }
    } catch (err) {
      console.error("Failed to prefill rows", err);
    } finally {
      await refresh();
    }
  };

  const handleSingleEntryChange = (field, value) => {
    setSingleEntryValues((prev) => ({
      ...prev,
      [field]: { ...(prev[field] || { content: "", image_data: null }), content: value }
    }));
  };

  const handleSingleEntryImage = async (field, file) => {
    if (!file) {
      setSingleEntryValues((prev) => ({
        ...prev,
        [field]: { ...(prev[field] || { content: "" }), image_data: null }
      }));
      return;
    }
    const dataUrl = await fileToDataUrl(file);
    setSingleEntryValues((prev) => ({
      ...prev,
      [field]: { ...(prev[field] || { content: "" }), image_data: dataUrl }
    }));
  };

  const handleSaveSingleEntry = async (field) => {
    const payload = singleEntryValues[field] || { content: "", image_data: null };
    try {
      await axios.post(`${API}/projects/${projectId}/single-entry`, {
        field_name: field,
        content: payload.content,
        image_data: payload.image_data
      });
      alert("Saved successfully!");
    } catch (err) {
      alert("Failed to save");
    }
  };

  return (
    <div>
      {showHeading && (
        <h2 style={{ fontSize: "1.5rem", fontWeight: "600", marginBottom: "1.5rem" }}>
          {sectionId} - {sectionName}
        </h2>
      )}

      {singleEntryDefinitions.length > 0 && (
        <div style={{ display: "grid", gap: "1.5rem", marginBottom: "2rem" }}>
          {singleEntryDefinitions.map((entry) => {
            const value = singleEntryValues[entry.field] || { content: "", image_data: null };
            return (
              <div
                className="card"
                key={entry.field}
                style={{ background: "#f7fafc", padding: "1.5rem" }}
              >
                <h3 style={{ fontSize: "1.1rem", fontWeight: "600", marginBottom: "0.75rem" }}>
                  {entry.label}
                </h3>
                {entry.description && (
                  <p style={{ color: "#718096", marginBottom: "0.75rem" }}>{entry.description}</p>
                )}
                <textarea
                  className="input"
                  rows="4"
                  value={value.content}
                  onChange={(e) => handleSingleEntryChange(entry.field, e.target.value)}
                  readOnly={!isEditor}
                  disabled={singleEntriesLoading}
                  placeholder={`Enter ${entry.label.toLowerCase()}...`}
                  style={{ marginBottom: entry.supportsImage ? "1rem" : "0" }}
                />
                {entry.supportsImage && (
                  <div style={{ marginBottom: "1rem" }}>
                    <label className="label" style={{ display: "block", marginBottom: "0.5rem" }}>
                      Attach Diagram / Image
                    </label>
                    <input
                      type="file"
                      accept="image/*"
                      onChange={async (event) => {
                        const file = event.target.files?.[0];
                        await handleSingleEntryImage(entry.field, file || null);
                      }}
                      disabled={!isEditor || singleEntriesLoading}
                    />
                    {value.image_data && (
                      <div style={{ marginTop: "0.75rem" }}>
                        <img
                          src={value.image_data}
                          alt={`${entry.label} visual`}
                          style={{ maxWidth: "100%", borderRadius: "0.5rem" }}
                        />
                        {isEditor && (
                          <button
                            className="btn btn-outline btn-sm"
                            style={{ marginTop: "0.75rem" }}
                            onClick={() => handleSingleEntryImage(entry.field, null)}
                          >
                            Remove Image
                          </button>
                        )}
                      </div>
                    )}
                  </div>
                )}
                {isEditor && (
                  <button
                    className="btn btn-primary btn-sm"
                    onClick={() => handleSaveSingleEntry(entry.field)}
                    disabled={singleEntriesLoading}
                  >
                    Save
                  </button>
                )}
              </div>
            );
          })}
        </div>
      )}

      {tables.length === 0 ? (
        <div className="info-message">
          {singleEntryDefinitions.length === 0
            ? "This section is available. Tables and fields can be configured as needed."
            : "No tabular data is configured for this section."}
        </div>
      ) : (
        <>
          <div style={{ display: "flex", gap: "0.5rem", marginBottom: "1.5rem", flexWrap: "wrap" }}>
            {tables.map((table) => (
              <button
                key={table.key}
                className={`btn btn-sm ${activeTableKey === table.key ? "btn-primary" : "btn-outline"}`}
                onClick={() => {
                  setActiveTableKey(table.key);
                  setEditingId(null);
                  setEditData({});
                  setNewRowData({});
                }}
                data-testid={`table-${table.key}`}
              >
                {table.title || table.name || table.key}
              </button>
            ))}
          </div>

          {activeTableDefinition && (
            <div>
              <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "1rem" }}>
                <h3 style={{ fontSize: "1.2rem", fontWeight: "600" }}>
                  {activeTableDefinition.title || activeTableDefinition.name}
                </h3>
                {isEditor && activeRows.length === 0 && activeTableDefinition.prefillRows && (
                  <button
                    className="btn btn-secondary btn-sm"
                    onClick={() => handlePrefillRows(activeTableDefinition)}
                  >
                    Populate Defaults
                  </button>
                )}
              </div>

              {tablesLoading ? (
                <div className="loading">Loading...</div>
              ) : (
                <div className="table-container">
                  <table className="data-table">
                    <thead>
                      <tr>
                        {activeTableDefinition.columns.map((column) => (
                          <th key={column.key}>{column.label}</th>
                        ))}
                        {isEditor && <th>Actions</th>}
                      </tr>
                    </thead>
                    <tbody>
                      {activeRows.length === 0 ? (
                        <tr>
                          <td
                            colSpan={activeTableDefinition.columns.length + (isEditor ? 1 : 0)}
                            style={{ textAlign: "center", padding: "2rem", color: "#718096" }}
                          >
                            No data available. {isEditor && "Use the controls below to add entries."}
                          </td>
                        </tr>
                      ) : (
                        activeRows.map((row) => (
                          <tr key={row.id}>
                            {activeTableDefinition.columns.map((column) => (
                              <td key={column.key}>
                                {editingId === row.id ? (
                                  <input
                                    type="text"
                                    className="input"
                                    style={{ padding: "0.5rem", fontSize: "0.875rem" }}
                                    value={editData[column.key] || ""}
                                    onChange={(e) =>
                                      setEditData((prev) => ({ ...prev, [column.key]: e.target.value }))
                                    }
                                  />
                                ) : (
                                  row[column.key] || "-"
                                )}
                              </td>
                            ))}
                            {isEditor && (
                              <td>
                                {editingId === row.id ? (
                                  <div style={{ display: "flex", gap: "0.5rem" }}>
                                    <button className="btn btn-success btn-sm" onClick={handleEditRow}>
                                      Save
                                    </button>
                                    <button
                                      className="btn btn-outline btn-sm"
                                      onClick={() => {
                                        setEditingId(null);
                                        setEditData({});
                                      }}
                                    >
                                      Cancel
                                    </button>
                                  </div>
                                ) : (
                                  <div style={{ display: "flex", gap: "0.5rem" }}>
                                    <button
                                      className="btn btn-primary btn-sm"
                                      onClick={() => {
                                        setEditingId(row.id);
                                        setEditData(row);
                                      }}
                                    >
                                      Edit
                                    </button>
                                    <button
                                      className="btn btn-danger btn-sm"
                                      onClick={() => handleDeleteRow(row.id)}
                                    >
                                      Delete
                                    </button>
                                  </div>
                                )}
                              </td>
                            )}
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                </div>
              )}

              {isEditor && (
                <div
                  className="card"
                  style={{ background: "#f7fafc", padding: "1.5rem", marginTop: "1.5rem" }}
                >
                  <h4 style={{ fontSize: "1rem", fontWeight: "600", marginBottom: "1rem" }}>
                    Add New Row
                  </h4>
                  <form
                    onSubmit={(event) => {
                      event.preventDefault();
                      handleAddRow();
                    }}
                    style={{ display: "grid", gap: "1rem" }}
                  >
                    {activeTableDefinition.columns.map((column) => (
                      <div className="form-group" key={column.key}>
                        <label className="label">{column.label}</label>
                        <input
                          type="text"
                          className="input"
                          value={newRowData[column.key] || ""}
                          onChange={(e) =>
                            setNewRowData((prev) => ({ ...prev, [column.key]: e.target.value }))
                          }
                          placeholder={`Enter ${column.label.toLowerCase()}`}
                        />
                      </div>
                    ))}
                    <div style={{ display: "flex", gap: "1rem", flexWrap: "wrap" }}>
                      <button type="submit" className="btn btn-primary btn-sm">
                        Add
                      </button>
                      <button
                        type="button"
                        className="btn btn-outline btn-sm"
                        onClick={() => setNewRowData({})}
                      >
                        Clear
                      </button>
                    </div>
                  </form>
                </div>
              )}
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default GenericSection;
